	module.exports = {"data":{"post":{"html":"<p>I was planning on taking vacation this week, but then at work we <a href=\"http://www.papascott.de/2002/09/26/1907.html\">agreed</a> to give back 3 vacation days. So I'm working for nothing this week, and Mama is home alone with Christopher the Wonder Boy.</p>\n<p>I had a bad dream about the US Postal Service last night. I'm not ususally so introspective as to blog about my dreams, but I found it interesting that after 12 years of German and European bureaucracy, my bureaucratic nightmares are still American.</p>","fields":{"title":"Bad Dreams","date":"16 Dec 2002","url":"https://papascott.de/2002-12-16-bad-dreams/"}}},"pathContext":{"slug":"/2002-12-16-bad-dreams/","prev":{"fields":{"title":"Buying Cheese","date":"15 Dec 2002","slug":"/2002-12-15-buying-cheese/"}},"next":{"fields":{"title":"US fury over documentary","date":"17 Dec 2002","slug":"/2002-12-17-us-fury-over-documentary/"}}}}

